<template>
  <div class="pane fifth-step">
    <h1>Resumen</h1>
    <div v-if="carShop && carShop.nombre !== 'Taller para Arreglo Directo'">
      <p>Valida y confirma tu informacin y la del taller que seleccionaste</p>

      <div class="summary">
        <div class="icon"></div>
        <div class="body">
          <h3>{{ carShop.nombre }}</h3>

          <span class="label">Direccin</span>
          <div class="info">{{ carShop.direccion }} </div>

          <span class="label">Email</span>
          <div class="info">{{ carShop.email }} </div>

          <span class="label">Telfono</span>
          <div class="info">{{ carShop.telefono }} </div>

          <!-- <span class="label">Horario de atencin:</span>
          <div class="info">
            <p>Lunes a Sbado</p>
            <p>de 8:00 a.m. a 5:00 p.m.</p>
          </div> -->

          <div class="alert">
            <strong>Recomendamos realizar el ingreso de tu vehculo en los prximos 10 das,
              recuerda contactar al taller para coordinar el ingreso</strong>
          </div>
        </div>
      </div>
    </div>
    <div v-else-if="claimType === 'CLAIM_TYPE_PTH'">
      <p>Lo que debes saber si tu vehculo fue hurtado</p>
      <div class="summary">
        <div class="icon"></div>
        <div class="body">
          <ol>
            <li>
              <p>
                Enva el denuncio por hurto al correo<strong>siniestros.autos@libertycolombia.com</strong>.
                Si yaobtuviste el certificado de no recuperacin de la fiscala, puedes enviarlo.
              </p>
            </li>
            <li>
              <p>
                A tu correo electrnico se enviar la informacin necesaria para continuar
                con el estudio del caso.
              </p>
            </li>
            <li>
              <p>
                El analista de tu caso se comunicar para informarte en detalle el proceso
                de indemnizacin y el trmite que se debe realizar ante trnsito.
              </p>
            </li>
            <li>
              <p>
                El proceso solo dar inicio cuando la documentacin este completa.
              </p>
            </li>
          </ol>
        </div>
      </div>
    </div>
    <div v-else>
      <p>Lo que debes saber para reparar el vehculo en tu taller de confianza</p>

      <div class="summary">
        <div class="icon"></div>
        <div class="body">
          <ol>
            <li class="list-item">
              <h3>Rene los siguientes documentos:</h3>
              <ul>
                <li>
                  <p>
                    Copia de informe de trnsito o croquis (si lo tienes) o fallo o
                    resolucin de trnsito o acuerdo conciliatorio o carta del asegurado
                    donde indique la versin de los hechos, fotografas que demuestren ocurrencia,
                    fecha, lugar, nombre del conductor y autorizacin para afectar su pliza.
                  </p>
                </li>
                <li>
                  <p>Fotografas claras de los daos del vehculo</p>
                </li>
                <li>
                  <p>Cotizacin de mano de obra y repuestos de tu taller de confianza</p>
                </li>
              </ul>
            </li>
            <li class="item-list">
              <h3>Enva los documentos</h3>
              <p>Al correo <span class="email-siniestros">siniestros.autos@libertycolombia.com.</span>
                Para asegurar la llegada del correo, recuerda que los archivos que enves no deben superar 8 MB </p>
            </li>
            <li class="list-item">
              <h3>Te informaremos por correo la respuesta del caso</h3>
            </li>
            <li class="list-item">
              <h3>Recibirs la notificacin del pago *</h3>
              <p>
                <strong>
                  * 1. Recuerda que este servicio opera por reembolso, por tanto, los documentos
                  que nos hagas llegar estarn sujetos a estudio por parte de nuestros ingenieros.
                  2. Una vez se autorice la cotizacin enviada, procederemos con el pago de un anticipo del 60%.
                  3. Al finalizar el arreglo, debers enviarnos los soportes de la reparacin de tu vehculo para concluir el pago del 40% restante.
                </strong>
              </p>
            </li>
          </ol>
        </div>
      </div>
    </div>

    <div class="actions">
      <a  href="#" v-on:click.prevent="prevStep(4)">Volver</a>
      <button v-on:click="submit" type="button">Finalizar</button>
    </div>

  </div>
</template>
<script>
import steps from '../mixins/steps';

export default {
  mixins: [steps],
  props: ['carShop', 'claimType'],
  methods: {
    submit: function () {
       this.$emit('submit');
        QSI.API.unload();
        QSI.API.load().then(QSI.API.run());
    }
  }
}
</script>



