<template>
  <div class="pane documents-step">
    <h1>Adjunta los siguientes documentos</h1>
    <p>Debes tenerlos todos para finalizar tu proceso</p>

    <div class="documents-list">
      <ul>
        <li>
          <p><strong>Tarjeta de propiedad o documento que acredite la propiedad del vehculo</strong>
          o bien afectado. Para este caso adjuntar copia de la cdula del asegurado.</p>
        </li>
        <li>
          <p>
            <strong>Cdula o Cmara de comercio</strong> del propietario del vehculo o bien afectado.
          </p>
        </li>
        <li>
          <p>
            <strong>Copia de informe de trnsito </strong> (croquis) o <strong>Fallo o resolucin de trnsito</strong> o
          <strong>Acuerdo conciliatorio</strong> o <strong>Carta del asegurado</strong> donde indique la versin de los hechos,
          fotografas que demuestren ocurrencia, fecha, luga, nombre del conductor y
          autorizacin para afectar su pliza. Para este caso adjuntar copia de la cdula del asegurado.
          </p>
        </li>
        <li v-show="caseType == 1">
          <p>
            Certificado de no reclamacin (se solicita en tu aseguradora)
          </p>
        </li>
        <li v-show="caseType == 2">
          <p>
            Declaracin extra juicio donde indiques que no se encuentra asegurado
          </p>
        </li>
        <li v-show="caseType == 3">
          <p>
            <strong>Factura del deducible pagado y certificacin del deducible</strong> <br/> (se solicita en la aseguradora)
          </p>
        </li>
        <li v-show="caseType == 1 || caseType == 2">
          <p>
            Cotizacin de mano de obra y repuestos y fotografas de los daos del vehculo
          </p>
        </li>
      </ul>
    </div>

    <div v-bind:class="{field: true, error: submited && hasError('files')}">
      <div class="label label-img-adj">Adjunta documentos o imagenes, formatos admitidos: pdf y jpg, mximo permitido: pdf - 2MB, jpg 60KB  (obligatorio)</div>
      <dropzone
        id="dropzone-documents"
        ref="dropzoneEl"
        :options="dropzoneOptions"
        :useCustomSlot="true"
        :duplicateCheck="true"
        @vdropzone-success="fileUploaded"
        @vdropzone-removed-file="fileRemoved"
        @vdropzone-file-added="fileAdded"
        v-if="userData">
        <span class='arch-desck'>Arrastra tus archivos para adjuntar, o <strong>bscalos aqu</strong></span>
        <span class='arch-mobile'>Adjunta tus archivos aqu</span>
      </dropzone>
      <div class="error-message" v-show="submited && hasError('files')">
        {{ errorMsg }}
      </div>
    </div>

    <div class="actions">
      <a  href="#" v-on:click.prevent="prevStep">Volver</a>
      <button v-on:click="submit" type="button">Finalizar</button>
    </div>

    <alert :open="modal" icon="invalid-plate" v-on:closeModal="closeModal($event)">
      <div slot="body">
        <div >
          <h2>{{ modalTitle}}</h2>
          <p> {{ modalBody }}</p>
        </div>
      </div>
    </alert>

  </div>
</template>
<script>
import steps from '../mixins/steps';
import vue2Dropzone from 'vue2-dropzone';
import 'vue2-dropzone/dist/vue2Dropzone.min.css';
import Alert from './../components/commons/alert';

export default {
  mixins: [steps],
  props: ['userData', 'caseType'],
  components: {
    'dropzone': vue2Dropzone,
    Alert,
  },
  computed: {
    dropzoneOptions: function() {
      if (this.userData) {
        return {
          url: '/claim/files/' + this.userData.documentId + '/save',
          thumbnailWidth: 150,
          acceptedFiles: 'image/jpeg, application/pdf',
          addRemoveLinks: true,
          /*resizeWidth: 600,
          resizeHeight: 400,*/
          dictFileTooBig: 'El archivo es demasiado grande ({{filesize}}MB), supera el mximo de {{maxFilesize}}MB.',
          createImageThumbnails: false,
          headers: {
            'token': this.drupalSettings.token
          }
        };
      }
      else {
        return {};
      }
    },
  },
  methods: {
    submit: function () {
      this.submited = true;

      if (this.isFormOk()) {
        this.$emit('submit');
      }
      else {
        this.errorMsg = 'Debes adjuntar mnimo ' + this.validationRules.files.length.min + ' archivo.';
      }
    },
     closeModal: function () {
      this.modal = false;
    }
  },
  watch: {
    caseType: function(val, oldVal) {
     /*  if (val == 3) {
        this.validationRules.files.length.equal = 4;

      }
      else {
        this.validationRules.files.length.equal = 5;
      } */
    }
  },
  data() {
    return {
      validationRules: {
        files: {
          length: { min: 1 }
        }
      },
      casualtyData: {
        files: [],
      },
      errorMsg: '',
      modal: false,
      modalBody: '',
      modalTitle: ''
    }
  },
  mounted () {

  }
}
</script>



